.app{
  width:100%;
  height:100vh;
  display:flex;
  flex-direction: column;
}
.navbar,.footer{
  width:100%;
  height:60px;
  display: flex;
  flex-direction: row;
  align-items: center;
 font-family:Lexend;
 position: fixed;
 background-color: white;
 z-index: 10;
}.navbar{
  top:0;
  left:0;
  box-shadow: 0px 0px 10px 0px rgb(218, 218, 218);
  justify-content: space-between;
  
}
.navbar h1{
  font-size: 18px;
  margin-left: 20px;
  display: flex;
  align-items: center;
  color: rgb(18, 18, 18);
}.navbar h1 i{
  margin-right: 10px;
  color:rgb(144, 144, 144);
  margin-top: 6px;
}
.outletdiv{
width:100%;
height:86%;
margin-top: 60px;
display: flex;
flex-direction: column;




}
.footer{
  justify-content: space-around;
  bottom: 0;
  left:0;
  padding-top: 10px;
}
.footer button,.restart{
  padding:15px;
  font-family: Comfortaa;
  color:rgb(0, 0, 0);
  width:130px;
  border-radius: 100px;
  border:2px solid rgb(0, 0, 0);
  font-weight: 900;
  transition-duration: 0.3s;
  background-color: white;
  display: flex;
  align-items: center;
 justify-content: space-evenly;



}
.footer button:hover,.restart:hover{
  background-color:rgb(0, 0, 0);
  color:rgb(255, 255, 255)
}
.quiztopbar{

  height: fit-content;
  padding-bottom: 20px;
background-color: rgb(245, 247, 250);
  font-family: Lexend;
  color:rgb(0, 0, 0);
  display: flex;
  flex-wrap:wrap;
  align-items: flex-start;
  justify-content: space-between;
  border-bottom: 2px solid rgb(236, 236, 236);

}.topic{
  padding:15px;
  font-size: 20px;
  width:fit-content;
  border-radius: 100px;
  font-weight: 400;
  font-family: Comfortaa;
  display: flex;
  align-items: center;
 
}.topic span{
  padding: 15px;
  border-radius: 100px;
  background-color: rgb(255, 255, 255);
  margin-inline: 10px;
  color:rgb(0, 0, 0);
  border:2px solid rgb(53, 53, 53);
  width:100px;
  text-align: center;
  
  

}
.time{
 
  color:black;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 100px;
  margin-right: 20px;
  padding: 15px;
  width: 100px;
}
.ques{
  width:90%;
  height:fit-content;
  padding-inline: 5%;
  text-align: center;
  align-self: center;
}.options{
 align-self: center;
width:90%;
display: flex;
flex-wrap: wrap;
justify-content: center;
padding-bottom: 100px;
margin-top: 30px;

}.options button{
  border-radius: 20px;
  padding:15px;
  background-color:rgb(255, 255, 255);
  font-family: Comfortaa;
  margin-top: 20px;
  border: 3px solid lightgrey;
  text-align: left;
  width:90%;
  max-width: 40%;
  min-width: 400px;
  margin:2%;
  font-size: 18px;
  line-height: 25px;
  color:grey;
  letter-spacing: 0.4px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;



}.options button:hover{
  border-color: black;
  color:black
}.options button i{
  margin-right: 20px;
  align-self: flex-start;
  margin-top: 10px;
}
.restart{
  margin-right: 15px;
}.progress{
  background-color: black;
  height:5px;
  position: fixed;
 top: 60px;
  border-radius: 100px;
  transition-duration: 0.5s;
}.quesno{
  width:95%;
  margin-left: 30px;
text-align: center;

}.scoreboard{
  width:90%;
padding-bottom: 30px;
align-self: center;
  font-family:Lexend;
     background:linear-gradient(130deg,rgb(216, 238, 255),rgb(231, 255, 248));
    margin-top: 40px;
    border-radius: 30px;


}.scoreboard h1{
  font-size: 30px;
  font-weight: 400;
  margin-left: 30px;
  
}.scoreboard h2{
  font-size: 20px;
  font-weight: 400;
  margin-left: 30px;
}.scoreboard p{
  font-size: 15px;
  font-weight: 400;
  margin-left: 30px;
  color:rgb(73, 73, 73)
}.scoreboard h3{
  font-size: 16px;
  font-weight: 400;
  margin-left: 30px;
  background-color: white;
  padding:15px;
  border-radius: 100px;
  width:fit-content;
  color:rgb(0, 0, 0);
  font-family: Lexend;


}.scoreboard button{
  padding:15px;
  font-size: 15px;
  border-radius: 100px;
  background-color: black;
  font-family: Lexend;
  color:white;
  width:130px;
  text-align: center;
  margin-left: 30px;
}
.overview {
  display: flex;
  justify-content: space-around;
  gap: 20px;
 width:90%;
 align-self: center;
 margin-top: 40px;
 font-family: Lexend;
}

.score-card {
  flex: 1;
  padding: 20px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  
}

.score-card p {
  font-size: 2rem;
  margin: 0;
  font-weight: bold;
}

.score-card span {
  display: block;
  margin-top: 5px;
  font-size: 1rem;
  color: #ffffff;
}

/* Colors */
.score-card.correct {
  background-color:dodgerblue;
  color: #ffffff;
}

.score-card.incorrect {
  background-color: #ff0a0a;
  color: #ffffff;
}

.score-card.unanswered {
  background-color: #cab300;
  color: #ffffff;
}
.sidebar{
  width:300px;
  background-color: white;
  position: fixed;
  right:0;
  top:0;
  height:100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y:auto ;
  scrollbar-width:none;


}.sidebar button{
  background-color: rgb(239, 241, 244);
  font-family: Lexend;
  border:0px;
  width:90%;
 border-radius: 20px;
 min-height: 70px;
  margin-top: 20px;
  padding: 10px;
  text-align: left;

}.sidebar h1{
  margin-top: 100px;
  font-size: 15px;
  font-family: Lexend;
  padding:10px;
  border: 2px solid black;
  border-radius: 50px;
  align-self: flex-start;
  margin-left: 20px;
  font-weight: 400;
}
.toggle{
  padding:15px;
  font-size: 15px;
  position: fixed;
  width:fit-content;
  top:5px;
  right:170px;
  z-index:10;
  background-color: rgb(0, 0, 0);
  border-radius: 100px;
  border: 0px;
  color:white;
  font-family: Comfortaa;
  
}.outputview{
  width:90%;
  align-self: center;
  border-radius: 20px;
  font-family: Lexend;

}.outputview p{
  padding:10px;
  background-color: rgb(212, 255, 212);
  border-radius: 40px;
  width:fit-content;
  width:130px;
  text-align: center;

}.outputview h1{
font-size: 20px;
}.outputview h2{
  font-size: 14px;
  color:grey;
  margin-top: 20px;
  font-weight: 400;
}.outputview div{
  margin-top: 70px;
  border-bottom: 2px solid rgb(220, 220, 220);
  padding-bottom: 20px;
}
@media(max-width:700px){
  .restart,.toggle{
    font-size: 12px;
    padding:10px;
    width:100px;
    top:10px;
    right:140px

  }.topic{
    font-size: 15px;
  }.time{
  background-color: white;
  margin-left: 30px;
  }
}